import { getElem, loop, onReady, onResize } from "../tools/utilities/helper-functions";
type Group = {isActive: boolean, elem: HTMLElement}
enum Speed {'slow' = 4, 'medium' = 6, 'fast' = 8}

const marquee = getElem('[data-marquee]')!
const groupElem = getElem('[data-marquee-group]')!
const speedValue = <'slow' | 'medium' | 'fast'>marquee.getAttribute('data-marquee-speed')
const speed: Speed = speedValue ? Speed[speedValue] : Speed['medium']
marquee && groupElem && (() => {

  groupElem.style.left = window.innerWidth+'px'

  let groups: Group[] = [{elem: groupElem, isActive: true}]
  let paused = false

  addGroupCopies(groupElem)
  startFrameLoop(slideActiveGroupsLeft)


  onResize(() => {
    addGroupCopies(groupElem)
    repositionGroups()
  })



  function repositionGroups(){
    groups.forEach((group, index) => {
      if(group.isActive ){
        const groupLeft = parseInt(group.elem.style.left)
        if(groupLeft > window.innerWidth){
          group.elem.style.left = window.innerWidth+'px'
        }

        const groupInFront = index - 1 < 0 ? 
        groups[groups.length-1] : groups[index-1]

        if(groupInFront.isActive){
          const endXVal = parseInt(groupInFront.elem.style.left) + groupInFront.elem.clientWidth
          group.elem.style.left = endXVal+'px'
        }

      }
      if(!group.isActive){
        group.elem.style.left = window.innerWidth+'px'
      }
    })

  }


  function startFrameLoop(action: () => void){
    doActionThenWaitForNextFrame()
    function doActionThenWaitForNextFrame(){
      action()
      requestAnimationFrame(doActionThenWaitForNextFrame)
    }
  }

  function slideActiveGroupsLeft(){
    groups.forEach((group, currentIndex) => {
      if(group.isActive){

        const width = group.elem.clientWidth
        const left = parseInt(group.elem.style.left)
        const hasEntered = left+width < window.innerWidth

        if(hasEntered){
          const isLastIndex = currentIndex + 1 == groups.length
          const nextGroup = isLastIndex ? 
            groups[0] : groups[currentIndex+1]
      
          if(!nextGroup.isActive){
            nextGroup.isActive = true
            nextGroup.elem.style.left = window.innerWidth+'px'
          }
        }        
        if(left > -width){
          group.elem.style.left = `${left - speed}px`
        } else {
          group.isActive = false
        }
      }
    })
  }


  function addGroupCopies(group: HTMLElement){
    const groupWidth = group.clientWidth
    let groupsNeeded = Math.ceil(window.innerWidth/groupWidth)
    if(groupsNeeded < 3) groupsNeeded = 3
    groupsNeeded -= groups.length
    if(groupsNeeded > 0){
      loop(groupsNeeded, () => {
        const newGroup = <HTMLElement>group.cloneNode(true)
        newGroup.style.left = window.innerWidth+'px'
        marquee.append(newGroup)
        groups.push({isActive: false, elem: newGroup})
      })
    }
  }
})()
